::-webkit-scrollbar {
    display: none;
}

.filter-container {
    display: flex;
    justify-content: space-between;
}

.all-container-for-categories-menu {
    margin-bottom: 60px;
    display: flex;
    margin-top: 28px;
}

.categories-container_link{
    float: left;
    text-align: center;
    padding: 7.5px 24.5px;
    text-decoration: none;
    border: 1.5px solid;
    border-radius: 30px;
    color: #4440F6;
    text-align: center;
    font-size: 12px;
    margin-right: 16px;
}
.categories-container_link:hover{
    background-color: #4440F6;
    color: #fff;
}

.categories-container_link_dropDown:hover {
    background-color: #fff;
    color: #4440F6;
}



.dropdown {
    float: left;
    overflow: hidden;
}

.dropdown .dropbtn {
    font-size: 12px;    
    border: none;
    outline: none;
    color: #4440F6;
    padding: 7.5px 24.5px;
    background-color: inherit;
    font-family: inherit;
    margin: 0;
    border: 1.5px solid;
    border-radius: 30px;
    width: 112px;
    height: 31px;
    
}
.navbar__link {
    font-family: 'Manrope';
font-style: normal;
font-weight: 400;
}

