.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
    
}

.dropdown-content a {
    float: none;
    color: #4440F6;
    padding: 7.5px;
    text-decoration: none;
    display: block;
    text-align: left;
    border: none;
    font-family: 'Manrope';
font-style: normal;
font-weight: 400;
}

.dropdown-content a:hover {
    border: none;
}

.dropdown:hover .dropdown-content {
    display: block;
    border-radius: 30px;
    list-style: none;
    padding-left: 24px;
    background-color: #fff;
}

.dropdown-content a:hover {
    text-decoration: underline;
}


.dropdown::-webkit-scrollbar {
    width: 0px;
    background-color: transparent;
  }

  .activee {
    background-color: #4440F6;
    color: #fff;
  }